[data-bg-img="bgimg5"] {
  --menu-bg: url(../images/menu-bg-images/bg-img5.jpg);
  @extend .bg-img;

  &[data-menu-styles="transparent"][data-theme-mode="dark"] {
    --menu-bg: url(../images/menu-bg-images/bg-img5.jpg);
  }
}

//bg-img5 end

[data-vertical-style="icontext"] {

  &[data-bg-img="bgimg1"],
  &[data-bg-img="bgimg2"],
  &[data-bg-img="bgimg3"],
  &[data-bg-img="bgimg4"],
  &[data-bg-img="bgimg5"] {
    .app-sidebar {
      &.sticky.sticky-pin {
        .main-sidebar-header {
          backdrop-filter: blur(30px);
        }
      }
    }
  }
}

/* Menu Background Image Styles */

/* Loader Styles */
[loader="disable"] {
  #loader {
    display: none;
  }
}

/* Loader Styles */

/* Start::boxed-styles */
[data-width="boxed"] {
  &[data-nav-layout="horizontal"] {

    .page-header,
    .main-sidebar,
    .main-header-container {
      width: 95%;
    }

    .app-content .container-fluid {
      width: 95%;
    }

    .main-menu-container {
      .slide-right {
        inset-inline-end: -2%;
      }

      .slide-left {
        inset-inline-start: -2%;
      }
    }
  }
}
[data-nav-layout="vertical"][data-nav-style="icon-hover"][data-toggled="icon-hover-closed"][data-theme-mode="dark"][data-menu-styles="light"]{
   .app-sidebar .main-sidebar-header .header-logo .toggle-logo {
    display: block;
    }
    .app-sidebar .main-sidebar-header .header-logo .toggle-dark {
      display: none;
      }
}
[data-theme-mode="dark"][data-menu-styles="light"][data-header-styles="light"]{
.app-header {
  border-block-end: 1px solid var(--menu-border-color);
}
}
/* End::boxed-styles */

[data-nav-layout="horizontal"][data-theme-mode="dark"][data-menu-styles="light"]{
  .main-menu-container .slide-right,.slide-left {
    background: #dde5ed !important;
  }
}

[data-nav-layout="horizontal"][data-theme-mode="dark"]{
  &[data-bg-img="bgimg1"],
  &[data-bg-img="bgimg2"],
  &[data-bg-img="bgimg3"],
  &[data-bg-img="bgimg4"],
  &[data-bg-img="bgimg5"]{
  .app-sidebar {
    border-block-end: 0px !important;
  }
}
}